@keyframes fadeInLeft {
    from {
        opacity: 0;
        transform: translateX(-10px);
    }
    to {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes fadeInDown {
    from {
        opacity: 0;
        transform: translateY(10px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.slide-active {
    opacity: 1; /* Initially hide the element */
    animation: fadeInUp 2.5s cubic-bezier(0.42, 0, 0.58, 1) forwards; /* Custom cubic-bezier for smoother animation */
    animation-delay: -1s; /* Negative delay to start animation slightly early */
}

@keyframes fadeInUp {
    0% {
        opacity: 0;
        transform: translateY(20px); /* Start slightly lower */
    }
    100% {
        opacity: 1;
        transform: translateY(0); /* End at the original position */
    }
}

.vayu_blocks_heading{
    animation: fadeInDown 1s ease-out;
}

.vayu_blocks_sub_heading {
    animation: fadeInDown 1s ease-out;
}

.vayu_blocks_slider_button1 {
    animation: fadeInDown 1s ease-out;
}

.vayu_blocks_slider_button2 {
    animation: fadeInDown 1s ease-out;
}



